import React, { Component, Fragment } from 'react';

import {
  Card,
  Row,
  Col,
  message,
  Modal,
  DatePicker
} from 'antd';
import { isEmpty } from 'lodash';
import moment from 'moment';

import { TableData, ColEditDelete, CartHeadAction } from 'components/common';
import Page from 'components/LayoutComponents/Page';
import { BtnDelete, BtnExport } from 'components/common/button';
import { getQueryParams } from 'utils/urlUtil';
import { searchStudents, deleteStudent } from 'services/student';
import { deleteStudentFromClass } from 'services/Candidate';
import { exportReportBusPaperStudent, exportReportMilitaryPendingPaperStudent } from 'services/exportTemplatePapers';
import SearchBar from './SearchBar';


import './style.scss';

const options = [
  {
    value: 'Hệ: Cao đẳng chính quy ',
    label: 'Hệ: Cao đẳng chính quy ',
    isLeaf: false,
  },
  {
    value: 'Hệ: Đại học chính quy ',
    label: 'Hệ: Đại học chính quy ',
    isLeaf: false,
  },
];

const { RangePicker } = DatePicker;

class FactorOfTraningScore extends Component {
  constructor(props) {
    super(props);
    this.state = {
      options,
      reload: 0,
      loading: false,
      paramSearch: {},
      showModal: false,
      selectedStudents: [],
      selectedRowKeys: [],
      yearNVQS: [moment(),moment().add(1,'y')],
      exportStudentId: false
    };
    this.title = 'Danh sách sinh viên';
    this.urlOfBtnEdit = '#/profile-student';
    this.urlOfBtnAdd = '#/factorOfTrainingScore/form/edit/';
    this.columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'code',
        key: 'code',
        defaultSortOrder: 'descend',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Nơi sinh',
        dataIndex: 'pobText',
        key: 'pobText',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        align: 'center',
        render: (text, record) => (
          <Fragment>
            <ColEditDelete
              hrefEdit={`#profile-student/form/edit/${record.studentId}`}
              onConfirmDelete={() => this.handleDelete(record.studentId)}
            />
            <BtnExport title='NVQS' onClick={() => this.showModal(record.studentId)} />
            <BtnExport title='Bus' onClick={() => this.exportBus(record.studentId)} />
            <BtnExport title='Vay vốn' /> 
          </Fragment>
        )
      },
    ];
    // rowSelection object indicates the need for row selection
    this.cssBetweenRow = { marginBottom: '8px' };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  onChangeYearNVQS = (value) => {
    console.log(value);
    this.setState({ yearNVQS: value });
  }

  handleDeleteStudentFromClass = () => {
    const { selectedStudents } = this.state;
    if(!isEmpty(selectedStudents)) {
      this.setState({ loading: true });
      const deleteStudentPromises = selectedStudents.map(
          student => deleteStudentFromClass({ classId: student.classId, studentId: student.studentId })
        );
        Promise.all(deleteStudentPromises).then(() => {
          message.success('Xóa thành công');
          this.setState({ reload: this.state.reload + 1, selectedRowKeys: [], loading: false });
        }).catch(() => { 
          message.error('Có lỗi xãy ra');
          this.setState({ loading: false });
        });
      
    }
  }

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    await deleteStudent(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  loadData = selectedOptions => {
    const targetOption = selectedOptions[selectedOptions.length - 1];
    targetOption.loading = true;

    // load options lazily
    setTimeout(() => {
      targetOption.loading = false;
      targetOption.children = [
        {
          label: `Khóa 1`,
          value: 'dynamic1',
        },
        {
          label: `Khóa 2`,
          value: 'dynamic2',
        },
      ];
      this.setState({
        options: [...this.state.options],
      });
    }, 1000);
  };

  exportBus = async studentId => {
    try {
      this.setState({
        loading: true,
      });
      await exportReportBusPaperStudent(studentId);
      this.setState({
        loading: false,
      });
    } catch (error) {
      console.log(error);
      this.setState({
        loading: false,
      });
    }
  };

  exportNVQS = async () => {
    const { exportStudentId, yearNVQS } = this.state;
    const fromYear = yearNVQS[0].year();
    const toYear = yearNVQS[1].year();
    if (fromYear < toYear) {
      try {
        this.setState({
          loading: true,
        });
        await exportReportMilitaryPendingPaperStudent(Number.parseInt(exportStudentId), yearNVQS[0].year(), yearNVQS[1].year());
        this.setState({
          loading: false,
        });
      } catch (error) {
        console.log(error);
        this.setState({
          loading: false,
        });
      }
    } else {
      message.warn('Năm bắt đầu không được nhỏ hơn Năm kết thúc!');
    }
  };

  showModal = studentId => {
    this.setState({
      showModal: true,
      exportStudentId: studentId
    });
  };

  hideModal = () => {
    this.setState({
      showModal: false,
    });
  };

  render() {
    const { selectedRowKeys, paramSearch: { trainTypeId, facultyId, courseId }, loading, showModal, yearNVQS } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: (selectedKeys, selectedStudents) => {
        this.setState({ selectedStudents, selectedRowKeys: selectedKeys });
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
    const btnDeleteEnable = selectedRowKeys.length > 0;
    
    return (
      <Card title={this.title}>
        <CartHeadAction>
          <BtnDelete disabled={!btnDeleteEnable} title="Xóa sinh viên khỏi lớp" onClick={this.handleDeleteStudentFromClass} />
        </CartHeadAction>
        <Row style={this.cssBetweenRow} gutter={16} className="flex-center">
          <Col span={24}>
            <SearchBar />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={searchStudents}
              columns={this.columns}
              rowSelection={rowSelection}
              allowGetData={!!trainTypeId && !!facultyId && !!courseId}
              loading={loading}
            />
          </Col>
        </Row>
        <Modal
          title="Năm NVQS"
          visible={showModal}
          onOk={this.hideModal}
          onCancel={this.hideModal}
        >
          <Row>
            <Col span={16}>
              <RangePicker 
                placeholder={['Từ Năm', 'Đến Năm']}
                mode={['year', 'year']}
                value={yearNVQS}
                format='YYYY'
                onChange={this.onChangeYearNVQS} 
                onPanelChange={this.onChangeYearNVQS} 
              />
            </Col>
            <Col span={4}>
              <BtnExport title='Xuất giấy NVQS' onClick={this.exportNVQS} />
            </Col>
          </Row>
        </Modal>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <FactorOfTraningScore />
  </Page>
);



